



























































































/* base start */
html {
  height: 100%;
  box-sizing: border-box;
  font-size: 100px;
  overflow-x: hidden;

}
*, *:before, *:after {
  box-sizing: inherit;
}
body {
  position: relative;
  height: 100%;
  margin-left: calc(100vw - 100%);
  font: 16px/2 "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial,sans-serif;
  background: url(./assets/bg.jpg);
}
a {
  color: #333;
  text-decoration: none;
}
.link {
  color: #0097da;
}
.link:hover {
  color: #33ace1;
}
.link:active {
  color: #008fcf;
}
button {
  outline: none;
}
article {
  font-size: 14px;
}
article h3 {
  position: relative;
  padding-top: 1em;
  padding-bottom: 0.3em;
  border-bottom: 1px dashed #ccc;
  font-size: 20px;
  color: #333;
}
article h4 {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 400;
}
article h4 + p,
article h4 + ul {
  margin-top: 0;
}
article ul,
article ol {
  padding-left: 1.5em;
  font-size: 14px;
}
article a {
  color: #0097da;
  padding-bottom: 0.1em;
}
article a:hover {
  color: #33ace1;
  border-bottom: 1px solid currentColor;
}
article a:active {
  color: #008fcf;
}
article img {
  max-width: 100%;
}
article blockquote {
  padding: 0 1em;
  margin: 0;
  border: 9px solid #ccc;
  border-top-width: 3px;
  border-bottom-width: 3px;
  border-radius: 20px;
  font-size: 12px;
  color: #333;
}
article strong {
  font-weight: 400;
  color: #f60;
}
article hr {
  border: 1px solid #eee;
}
article pre {
  padding: 16px;
  border: 1px dotted #999;
  overflow: auto;
  font-size: 12px;
  border-radius: 0.5em;
  word-wrap: break-word;
}
article code {
  padding: 0.1em 0.5em;
  border: 1px dashed #999;
  border-radius: 3px;
  font: 12px Consolas, "Liberation Mono", Menlo, Courier, monospace;
  color: #666;
}
article pre code {
  padding: 0;
  border: none;
  background: transparent;
}
.hljs-built_in, .hljs-params, .hljs-number, .hljs-literal {
  color: #0086b3
}
.hljs-string {
  color: #183691;
}
.hljs-keyword, .hljs-meta, .hljs-name {
  color: #a71d5d;
}
.hljs-title {
  color: #795da3;
}
.hljs-comment {
  color: #969896;
}
.hljs-regexp {
  color: #183691;
}
.issues-content {
  padding: 0.3rem 0;
}
.issues-content li p,
.issues-content blockquote p {
  margin-left: 0;
  margin-right: 0;
}
.issues-content__title {
  position: relative;
  margin: 0;
  text-align: center;
  font-size: 24px;
}
.issues-content__time {
  margin-top: 0;
  margin-bottom: 2em;
  font-size: 12px;
  text-align: center;
  color: #999;
}
/* base end */

/* tool start */
.position-vertical-center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.transition-color-btn {
  padding: 0.3em 1em;
  border-radius: 3px;
  font-size: 12px;
  -webkit-transition-property: color, background-color;
          transition-property: color, background-color;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}
.center-prompt-message {
  font-size: 14px;
  text-align: center;
  color: #999;
}
/* tool end */

/* app start */
.app-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  width: 7rem;
  min-height: 100%;
  margin: 0 auto;
}
.app-header {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  position: relative;
  border-bottom: 1px solid #999;
}
.app-header nav {
  position: relative;
  font-size: 14px;
}
.app-header nav a {
  position: relative;
  display: table-cell;
  width: 0.7rem;
  text-align: center;
  line-height: 2;
}
.app-header nav a:hover,
.app-header nav .v-link-active {
  color: #0097da;
}
.app-header nav a:before {
  position: absolute;
  width: 100%;
  height: 0;
  left: 0;
  bottom: -1px;
  content: '';
  border-bottom: 3px solid #33ace1;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: -webkit-transform .3s ease;
          transition: -webkit-transform .3s ease;
          transition: transform .3s ease;
          transition: transform .3s ease, -webkit-transform .3s ease;
}
.app-header nav a:hover:before,
.app-header nav .v-link-active:before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.app-container {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  position: relative;
}
.app-footer {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  border-top: 1px solid #999;
  text-align: center;
  font-size: 12px;
  color: #999;
}
.app-tools__top {
  display: none;
  position: fixed;
  bottom: 0.5rem;
  color: #999;
  cursor: pointer;
}
.app-tools__top:hover {
  color: #f60;
}
.app-tools__top:active {
  color: #f26100;
}
.app-tools__top svg {
  fill: currentColor;
}
/* app end */

/*!
 * Load Awesome v1.1.0 (http://github.danielcardoso.net/load-awesome/)
 * Copyright 2015 Daniel Cardoso <@DanielCardoso>
 * Licensed under MIT
 */
.la-ball-clip-rotate,
.la-ball-clip-rotate > div {
  box-sizing: border-box;
}
.la-ball-clip-rotate {
  position: absolute;
  top: 0.2rem;
  left: 3.34rem;
  text-align: center;
  color: #0097da;
  z-index: 99;
}
.la-ball-clip-rotate.la-dark {
  color: #333;
}
.la-ball-clip-rotate > div {
  position: relative;
  display: inline-block;
  float: none;
  background: currentColor;
  border: 0 solid currentColor;
  width: 32px;
  height: 32px;
  background: transparent;
  border-width: 2px;
  border-bottom-color: transparent;
  border-radius: 100%;
  -webkit-animation: ball-clip-rotate .75s linear infinite;
  animation: ball-clip-rotate .75s linear infinite;
}
.la-ball-clip-rotate.la-sm {
  width: 16px;
  height: 16px;
}
.la-ball-clip-rotate.la-sm > div {
  width: 16px;
  height: 16px;
  border-width: 1px;
}
.la-ball-clip-rotate.la-2x {
  width: 64px;
  height: 64px;
}
.la-ball-clip-rotate.la-2x > div {
  width: 64px;
  height: 64px;
  border-width: 4px;
}
.la-ball-clip-rotate.la-3x {
  width: 96px;
  height: 96px;
}
.la-ball-clip-rotate.la-3x > div {
  width: 96px;
  height: 96px;
  border-width: 6px;
}
/*
* Animation
*/
@-webkit-keyframes ball-clip-rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes ball-clip-rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
/* Load Awesome end */

/* transition animation start */
@-webkit-keyframes fadeIn {
  from {opacity: 0;}
  to {opacity: 1;}
}
@keyframes fadeIn {
  from {opacity: 0;}
  to {opacity: 1;}
}
@-webkit-keyframes fadeOut {
  from {opacity: 1;}
  to {opacity: 0;}
}
@keyframes fadeOut {
  from {opacity: 1;}
  to {opacity: 0;}
}
.fadeInOut-enter {
  -webkit-animation: fadeIn .5s;
  animation: fadeIn .5s;
}
.fadeInOut-leave {
  -webkit-animation: fadeOut .5s;
  animation: fadeOut .5s;
}
@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate(0, 100%);
    transform: translate(0, 100%);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate(0, 100%);
    transform: translate(0, 100%);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@-webkit-keyframes fadeOutDown {
  from {opacity: 1;}
  to {
    opacity: 0;
    -webkit-transform: translate(0, 150%);
    transform: translate(0, 150%);
  }
}
@keyframes fadeOutDown {
  from {opacity: 1;}
  to {
    opacity: 0;
    -webkit-transform: translate(0, 150%);
    transform: translate(0, 150%);
  }
}
.fadeUpDown-enter {
  -webkit-animation: fadeInUp .3s;
  animation: fadeInUp .3s;
}
.fadeUpDown-leave {
  -webkit-animation: fadeOutDown .3s;
  animation: fadeOutDown .3s;
}
@-webkit-keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }
  50% {opacity: 1;}
}
@keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }
  50% {opacity: 1;}
}
@-webkit-keyframes zoomOut {
  from {opacity: 1;}
  50% {
    opacity: 0;
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }
  to {opacity: 0;}
}
@keyframes zoomOut {
  from {opacity: 1;}
  50% {
    opacity: 0;
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }
  to {opacity: 0;}
}
.zoomInOut-enter {
  -webkit-animation: zoomIn 0.3s;
  animation: zoomIn 0.3s;
}
.zoomInOut-leave {
  -webkit-animation: zoomOut 0.3s;
  animation: zoomOut 0.3s;
}
/* transition animation end */



